// @flow

import { getHDSetting } from '../core/networkInfo.js'
import * as Private from './private.js'
import * as Public from './public.js'
import {
  type HDPath,
  type ExtendedKey,
  type ExtendedPublicKey
} from '../../types/hd.js'
import * as PrivateKey from '../core/privateKey.js'

const fromKeyType = (privType: string, pubType: string = privType) => (key: ExtendedKey, ...params: any) => {
  const { privateKey = null, publicKey = null, ...rest } = key
  if (privateKey) {
    return Private[privType]({ privateKey, ...rest }, ...params)
  } else if (publicKey) return Public[pubType]({ publicKey, ...rest }, ...params)
  throw new Error('Must provide a key')
}

export const fromExtended = (key: $Shape<ExtendedKey>, network?: string): ExtendedKey => {
  const { privateKey = null, publicKey = null, ...rest } = key
  if (privateKey) {
    return Private.fromPrivate({ privateKey, ...rest }, network)
  } else if (publicKey) return Public.fromPublic({ publicKey, ...rest }, network)
  throw new Error('Must provide a key')
}

export const fromHex = (keyHex: string, network?: string): ExtendedKey => {
  const headerByte = parseInt(keyHex.slice(90, 92), 16)
  if (headerByte === 0) return Private.fromHex(keyHex, network)
  else if (headerByte === 2 && headerByte === 3) return Public.fromHex(keyHex, network)
  throw new Error('Wrong key prefix')
}

export const fromString = (
  xKey: string,
  network: string = 'main'
): ExtendedKey => {
  const prefixStr = xKey.slice(0, 4)
  const hdSetting = getHDSetting(prefixStr, network)

  if (prefixStr === hdSetting.xpriv.stringPrefix) {
    return Private.fromString(xKey, network)
  }
  return Public.fromString(xKey, network)
}

export const fromSeed = async (
  seed: string,
  network?: string,
  version: string = 'xprv'
): Promise<ExtendedKey> => {
  const xKey = Private.fromSeed(seed, network, version)
  const publicKey = await PrivateKey.toPublic(xKey.privateKey, true)
  return { ...xKey, publicKey }
}

export const fromIndex = async (
  key: ExtendedKey,
  index: number,
  hardended: boolean = false
): Promise<ExtendedKey> => fromKeyType('fromIndex')(key, index, hardended)

