export const fromPath = async (key: ExtendedKey, path: HDPath | string) => {
  const { privateKey = null, publicKey = null, ...rest } = key
  if (privateKey) {
    return Private.fromPath({ privateKey, ...rest }, path)
  } else if (publicKey) return Public.fromPath({ publicKey, ...rest }, path)
  throw new Error('Must provide a key')
}

export const toHex = (key: ExtendedKey): string => {
  const { privateKey = null, publicKey = null, ...rest } = key
  if (privateKey) {
    return Private.toHex({ privateKey, ...rest })
  } else if (publicKey) return Public.toHex({ publicKey, ...rest })
  throw new Error('Must provide a key')
}

export const toString = (
  key: ExtendedKey,
  network: string = 'main'
): string => {
  const { privateKey = null, publicKey = null, ...rest } = key
  if (privateKey) {
    return Private.toString({ privateKey, ...rest }, network)
  } else if (publicKey) return Public.toString({ publicKey, ...rest }, network)
  throw new Error('Must provide a key')
}

export const toPublic = async (hdKey: ExtendedKey, network?: string): Promise<ExtendedPublicKey> => {
  if (!hdKey.privateKey && hdKey.publicKey) return hdKey
  return Private.toPublic(hdKey, network)
}
